'use client'

import { useState } from 'react'
import { usePetData } from '@/context/PetDataContext'
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/card'
import { Button } from '@/components/ui/button'
import { Input } from '@/components/ui/input'
import { Label } from '@/components/ui/label'
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from '@/components/ui/select'
import { Badge } from '@/components/ui/badge'

export default function PetProfile() {
  const { petData, dispatch } = usePetData()
  const [isEditingPet, setIsEditingPet] = useState(false)
  const [isEditingOwner, setIsEditingOwner] = useState(false)
  const [editedPet, setEditedPet] = useState(petData.pet)
  const [editedOwner, setEditedOwner] = useState(petData.pet.owner)

  const handleSavePet = () => {
    dispatch({
      type: 'UPDATE_PET',
      payload: editedPet
    })
    setIsEditingPet(false)
  }

  const handleCancelPet = () => {
    setEditedPet(petData.pet)
    setIsEditingPet(false)
  }

  const handleSaveOwner = () => {
    dispatch({
      type: 'UPDATE_PET',
      payload: { owner: editedOwner }
    })
    setIsEditingOwner(false)
  }

  const handleCancelOwner = () => {
    setEditedOwner(petData.pet.owner)
    setIsEditingOwner(false)
  }

  const getAgeCategory = (age: number) => {
    if (age < 1) return { category: 'Puppy/Kitten', color: 'bg-green-100 text-green-800 dark:bg-green-900 dark:text-green-300' }
    if (age < 3) return { category: 'Young', color: 'bg-blue-100 text-blue-800 dark:bg-blue-900 dark:text-blue-300' }
    if (age < 7) return { category: 'Adult', color: 'bg-purple-100 text-purple-800 dark:bg-purple-900 dark:text-purple-300' }
    return { category: 'Senior', color: 'bg-orange-100 text-orange-800 dark:bg-orange-900 dark:text-orange-300' }
  }

  const ageInfo = getAgeCategory(petData.pet.age)

  return (
    <div className="space-y-6">
      <div>
        <h1 className="text-3xl font-bold text-gray-900 dark:text-white">
          Pet Profile
        </h1>
        <p className="text-gray-600 dark:text-gray-400 mt-1">
          Manage {petData.pet.name}'s basic information and owner details
        </p>
      </div>

      {/* Pet Information Card */}
      <Card>
        <CardHeader>
          <div className="flex items-center justify-between">
            <div>
              <CardTitle className="text-xl flex items-center space-x-2">
                <span>{petData.pet.name}</span>
                <Badge className={ageInfo.color}>
                  {ageInfo.category}
                </Badge>
              </CardTitle>
              <CardDescription>
                {petData.pet.breed} â¢ {petData.pet.type === 'dog' ? 'Dog' : 'Cat'} â¢ {petData.pet.age} years old
              </CardDescription>
            </div>
            {!isEditingPet ? (
              <Button onClick={() => setIsEditingPet(true)}>
                Edit Pet Info
              </Button>
            ) : (
              <div className="space-x-2">
                <Button variant="outline" onClick={handleCancelPet}>
                  Cancel
                </Button>
                <Button onClick={handleSavePet}>
                  Save Changes
                </Button>
              </div>
            )}
          </div>
        </CardHeader>
        <CardContent>
          {!isEditingPet ? (
            <div className="grid md:grid-cols-2 gap-6">
              <div className="space-y-4">
                <div>
                  <Label className="text-sm font-medium text-gray-500 dark:text-gray-400">
                    Pet Name
                  </Label>
                  <p className="text-lg font-semibold text-gray-900 dark:text-white">
                    {petData.pet.name}
                  </p>
                </div>
                <div>
                  <Label className="text-sm font-medium text-gray-500 dark:text-gray-400">
                    Type
                  </Label>
                  <p className="text-lg text-gray-900 dark:text-white capitalize">
                    {petData.pet.type}
                  </p>
                </div>
                <div>
                  <Label className="text-sm font-medium text-gray-500 dark:text-gray-400">
                    Breed
                  </Label>
                  <p className="text-lg text-gray-900 dark:text-white">
                    {petData.pet.breed}
                  </p>
                </div>
                <div>
                  <Label className="text-sm font-medium text-gray-500 dark:text-gray-400">
                    Color
                  </Label>
                  <p className="text-lg text-gray-900 dark:text-white">
                    {petData.pet.color}
                  </p>
                </div>
              </div>
              <div className="space-y-4">
                <div>
                  <Label className="text-sm font-medium text-gray-500 dark:text-gray-400">
                    Age
                  </Label>
                  <p className="text-lg text-gray-900 dark:text-white">
                    {petData.pet.age} years old
                  </p>
                </div>
                <div>
                  <Label className="text-sm font-medium text-gray-500 dark:text-gray-400">
                    Weight
                  </Label>
                  <p className="text-lg text-gray-900 dark:text-white">
                    {petData.pet.weight} lbs
                  </p>
                </div>
                <div>
                  <Label className="text-sm font-medium text-gray-500 dark:text-gray-400">
                    Microchip ID
                  </Label>
                  <p className="text-lg font-mono text-gray-900 dark:text-white">
                    {petData.pet.microchipId}
                  </p>
                </div>
              </div>
            </div>
          ) : (
            <div className="grid md:grid-cols-2 gap-6">
              <div className="space-y-4">
                <div>
                  <Label htmlFor="petName">Pet Name</Label>
                  <Input
                    id="petName"
                    value={editedPet.name}
                    onChange={(e) => setEditedPet({ ...editedPet, name: e.target.value })}
                    placeholder="Pet's name"
                  />
                </div>
                <div>
                  <Label htmlFor="petType">Type</Label>
                  <Select
                    value={editedPet.type}
                    onValueChange={(value: 'dog' | 'cat') => setEditedPet({ ...editedPet, type: value })}
                  >
                    <SelectTrigger>
                      <SelectValue />
                    </SelectTrigger>
                    <SelectContent>
                      <SelectItem value="dog">Dog</SelectItem>
                      <SelectItem value="cat">Cat</SelectItem>
                    </SelectContent>
                  </Select>
                </div>
                <div>
                  <Label htmlFor="breed">Breed</Label>
                  <Input
                    id="breed"
                    value={editedPet.breed}
                    onChange={(e) => setEditedPet({ ...editedPet, breed: e.target.value })}
                    placeholder="Pet's breed"
                  />
                </div>
                <div>
                  <Label htmlFor="color">Color</Label>
                  <Input
                    id="color"
                    value={editedPet.color}
                    onChange={(e) => setEditedPet({ ...editedPet, color: e.target.value })}
                    placeholder="Pet's color"
                  />
                </div>
              </div>
              <div className="space-y-4">
                <div>
                  <Label htmlFor="age">Age (years)</Label>
                  <Input
                    id="age"
                    type="number"
                    value={editedPet.age}
                    onChange={(e) => setEditedPet({ ...editedPet, age: parseInt(e.target.value) || 0 })}
                    placeholder="Pet's age"
                    min="0"
                    max="30"
                  />
                </div>
                <div>
                  <Label htmlFor="weight">Weight (lbs)</Label>
                  <Input
                    id="weight"
                    type="number"
                    value={editedPet.weight}
                    onChange={(e) => setEditedPet({ ...editedPet, weight: parseFloat(e.target.value) || 0 })}
                    placeholder="Pet's weight"
                    min="0"
                    step="0.1"
                  />
                </div>
                <div>
                  <Label htmlFor="microchipId">Microchip ID</Label>
                  <Input
                    id="microchipId"
                    value={editedPet.microchipId}
                    onChange={(e) => setEditedPet({ ...editedPet, microchipId: e.target.value })}
                    placeholder="Microchip identification number"
                  />
                </div>
              </div>
            </div>
          )}
        </CardContent>
      </Card>

      {/* Owner Information Card */}
      <Card>
        <CardHeader>
          <div className="flex items-center justify-between">
            <CardTitle className="text-xl">Owner Information</CardTitle>
            {!isEditingOwner ? (
              <Button onClick={() => setIsEditingOwner(true)}>
                Edit Owner Info
              </Button>
            ) : (
              <div className="space-x-2">
                <Button variant="outline" onClick={handleCancelOwner}>
                  Cancel
                </Button>
                <Button onClick={handleSaveOwner}>
                  Save Changes
                </Button>
              </div>
            )}
          </div>
        </CardHeader>
        <CardContent>
          {!isEditingOwner ? (
            <div className="grid md:grid-cols-2 gap-6">
              <div className="space-y-4">
                <div>
                  <Label className="text-sm font-medium text-gray-500 dark:text-gray-400">
                    Owner Name
                  </Label>
                  <p className="text-lg font-semibold text-gray-900 dark:text-white">
                    {petData.pet.owner.name}
                  </p>
                </div>
                <div>
                  <Label className="text-sm font-medium text-gray-500 dark:text-gray-400">
                    Phone Number
                  </Label>
                  <p className="text-lg text-gray-900 dark:text-white">
                    {petData.pet.owner.phone}
                  </p>
                </div>
              </div>
              <div className="space-y-4">
                <div>
                  <Label className="text-sm font-medium text-gray-500 dark:text-gray-400">
                    Email Address
                  </Label>
                  <p className="text-lg text-gray-900 dark:text-white">
                    {petData.pet.owner.email}
                  </p>
                </div>
              </div>
            </div>
          ) : (
            <div className="grid md:grid-cols-2 gap-6">
              <div className="space-y-4">
                <div>
                  <Label htmlFor="ownerName">Owner Name</Label>
                  <Input
                    id="ownerName"
                    value={editedOwner.name}
                    onChange={(e) => setEditedOwner({ ...editedOwner, name: e.target.value })}
                    placeholder="Owner's full name"
                  />
                </div>
                <div>
                  <Label htmlFor="phone">Phone Number</Label>
                  <Input
                    id="phone"
                    value={editedOwner.phone}
                    onChange={(e) => setEditedOwner({ ...editedOwner, phone: e.target.value })}
                    placeholder="Phone number"
                  />
                </div>
              </div>
              <div className="space-y-4">
                <div>
                  <Label htmlFor="email">Email Address</Label>
                  <Input
                    id="email"
                    type="email"
                    value={editedOwner.email}
                    onChange={(e) => setEditedOwner({ ...editedOwner, email: e.target.value })}
                    placeholder="Email address"
                  />
                </div>
              </div>
            </div>
          )}
        </CardContent>
      </Card>

      {/* Pet Statistics */}
      <Card>
        <CardHeader>
          <CardTitle>Pet Statistics</CardTitle>
          <CardDescription>Overview of {petData.pet.name}'s records and data</CardDescription>
        </CardHeader>
        <CardContent>
          <div className="grid grid-cols-2 md:grid-cols-4 gap-4">
            <div className="text-center p-4 bg-blue-50 dark:bg-blue-900/20 rounded-lg">
              <div className="text-2xl font-bold text-blue-600 dark:text-blue-400">
                {petData.healthRecords.length}
              </div>
              <div className="text-sm text-gray-600 dark:text-gray-400">
                Health Records
              </div>
            </div>
            <div className="text-center p-4 bg-green-50 dark:bg-green-900/20 rounded-lg">
              <div className="text-2xl font-bold text-green-600 dark:text-green-400">
                {petData.activities.length}
              </div>
              <div className="text-sm text-gray-600 dark:text-gray-400">
                Activities Logged
              </div>
            </div>
            <div className="text-center p-4 bg-purple-50 dark:bg-purple-900/20 rounded-lg">
              <div className="text-2xl font-bold text-purple-600 dark:text-purple-400">
                {petData.medications.filter(m => m.active).length}
              </div>
              <div className="text-sm text-gray-600 dark:text-gray-400">
                Active Medications
              </div>
            </div>
            <div className="text-center p-4 bg-orange-50 dark:bg-orange-900/20 rounded-lg">
              <div className="text-2xl font-bold text-orange-600 dark:text-orange-400">
                {petData.vaccinations.length}
              </div>
              <div className="text-sm text-gray-600 dark:text-gray-400">
                Vaccinations
              </div>
            </div>
          </div>
        </CardContent>
      </Card>

      {/* Quick Facts */}
      <Card>
        <CardHeader>
          <CardTitle>Quick Facts</CardTitle>
          <CardDescription>Key information about {petData.pet.name}</CardDescription>
        </CardHeader>
        <CardContent>
          <div className="grid md:grid-cols-2 gap-6">
            <div className="space-y-3">
              <div className="flex justify-between items-center p-3 bg-gray-50 dark:bg-gray-800 rounded-lg">
                <span className="text-sm font-medium">Pet ID</span>
                <span className="text-sm font-mono">{petData.pet.id}</span>
              </div>
              <div className="flex justify-between items-center p-3 bg-gray-50 dark:bg-gray-800 rounded-lg">
                <span className="text-sm font-medium">Age Category</span>
                <Badge className={ageInfo.color}>{ageInfo.category}</Badge>
              </div>
              <div className="flex justify-between items-center p-3 bg-gray-50 dark:bg-gray-800 rounded-lg">
                <span className="text-sm font-medium">Total Records</span>
                <span className="text-sm font-semibold">
                  {petData.healthRecords.length + petData.activities.length + petData.medications.length + petData.vaccinations.length}
                </span>
              </div>
            </div>
            <div className="space-y-3">
              <div className="flex justify-between items-center p-3 bg-gray-50 dark:bg-gray-800 rounded-lg">
                <span className="text-sm font-medium">Insurance Status</span>
                <Badge className="bg-green-500 text-white">Covered</Badge>
              </div>
              <div className="flex justify-between items-center p-3 bg-gray-50 dark:bg-gray-800 rounded-lg">
                <span className="text-sm font-medium">Microchipped</span>
                <Badge className="bg-blue-500 text-white">Yes</Badge>
              </div>
              <div className="flex justify-between items-center p-3 bg-gray-50 dark:bg-gray-800 rounded-lg">
                <span className="text-sm font-medium">Smart Tag</span>
                <Badge className="bg-green-500 text-white">Connected</Badge>
              </div>
            </div>
          </div>
        </CardContent>
      </Card>
    </div>
  )
}
