'use client'

import { useState } from 'react'
import { usePetData } from '@/context/PetDataContext'
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/card'
import { Button } from '@/components/ui/button'
import { Input } from '@/components/ui/input'
import { Label } from '@/components/ui/label'
import { Textarea } from '@/components/ui/textarea'
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from '@/components/ui/select'
import { Badge } from '@/components/ui/badge'
import { Dialog, DialogContent, DialogDescription, DialogHeader, DialogTitle, DialogTrigger } from '@/components/ui/dialog'

export default function HealthRecords() {
  const { petData, dispatch } = usePetData()
  const [isAddingRecord, setIsAddingRecord] = useState(false)
  const behavioralSymptoms = [
    'Lethargy',
    'Hiding',
    'Changes in Appetite',
    'Increased Vocalization',
    'Aggression'
  ]

  const [newRecord, setNewRecord] = useState({
    date: new Date().toISOString().split('T')[0],
    type: 'checkup' as 'checkup' | 'illness' | 'behavior' | 'injury',
    description: '',
    veterinarian: '',
    notes: '',
    behaviorSymptoms: [] as string[]
  })

  const handleAddRecord = () => {
    if (newRecord.type === 'behavior' ? newRecord.behaviorSymptoms.length > 0 : newRecord.description.trim()) {
      dispatch({
        type: 'ADD_HEALTH_RECORD',
        payload: {
          id: Date.now().toString(),
          ...newRecord,
          description: newRecord.type === 'behavior' ? newRecord.behaviorSymptoms.join(', ') : newRecord.description
        }
      })
      setNewRecord({
        date: new Date().toISOString().split('T')[0],
        type: 'checkup',
        description: '',
        veterinarian: '',
        notes: '',
        behaviorSymptoms: []
      })
      setIsAddingRecord(false)
    }
  }

  const getTypeColor = (type: string) => {
    switch (type) {
      case 'checkup': return 'bg-blue-100 text-blue-800 dark:bg-blue-900 dark:text-blue-300'
      case 'illness': return 'bg-red-100 text-red-800 dark:bg-red-900 dark:text-red-300'
      case 'behavior': return 'bg-green-100 text-green-800 dark:bg-green-900 dark:text-green-300'
      case 'injury': return 'bg-orange-100 text-orange-800 dark:bg-orange-900 dark:text-orange-300'
      default: return 'bg-gray-100 text-gray-800 dark:bg-gray-900 dark:text-gray-300'
    }
  }

  const sortedRecords = [...petData.healthRecords].sort((a, b) => 
    new Date(b.date).getTime() - new Date(a.date).getTime()
  )

  return (
    <div className="space-y-6">
      <div className="flex items-center justify-between">
        <div>
          <h1 className="text-3xl font-bold text-gray-900 dark:text-white">
            Health Records
          </h1>
          <p className="text-gray-600 dark:text-gray-400 mt-1">
            Medical history and behavioral observations for {petData.pet.name}
          </p>
        </div>
        <Dialog open={isAddingRecord} onOpenChange={setIsAddingRecord}>
          <DialogTrigger asChild>
            <Button>Add New Record</Button>
          </DialogTrigger>
          <DialogContent className="sm:max-w-[425px]">
            <DialogHeader>
              <DialogTitle>Add Health Record</DialogTitle>
              <DialogDescription>
                Add a new health record or behavioral observation for {petData.pet.name}.
              </DialogDescription>
            </DialogHeader>
            <div className="grid gap-4 py-4">
              <div className="grid grid-cols-4 items-center gap-4">
                <Label htmlFor="date" className="text-right">
                  Date
                </Label>
                <Input
                  id="date"
                  type="date"
                  value={newRecord.date}
                  onChange={(e) => setNewRecord({ ...newRecord, date: e.target.value })}
                  className="col-span-3"
                />
              </div>
              <div className="grid grid-cols-4 items-center gap-4">
                <Label htmlFor="type" className="text-right">
                  Type
                </Label>
                <Select
                  value={newRecord.type}
                  onValueChange={(value: any) => setNewRecord({ ...newRecord, type: value })}
                >
                  <SelectTrigger className="col-span-3">
                    <SelectValue />
                  </SelectTrigger>
                  <SelectContent>
                    <SelectItem value="checkup">Checkup</SelectItem>
                    <SelectItem value="illness">Illness</SelectItem>
                    <SelectItem value="behavior">Behavior</SelectItem>
                    <SelectItem value="injury">Injury</SelectItem>
                  </SelectContent>
                </Select>
              </div>
              <div className="grid grid-cols-4 items-center gap-4">
                <Label htmlFor="description" className="text-right">
                  Description
                </Label>
                {newRecord.type === 'behavior' ? (
                  <div className="col-span-3 space-y-1">
                    {behavioralSymptoms.map((symptom) => (
                      <label key={symptom} className="inline-flex items-center space-x-2">
                        <input
                          type="checkbox"
                          checked={newRecord.behaviorSymptoms.includes(symptom)}
                          onChange={(e) => {
                            const checked = e.target.checked
                            setNewRecord((prev) => {
                              const symptoms = new Set(prev.behaviorSymptoms)
                              if (checked) symptoms.add(symptom)
                              else symptoms.delete(symptom)
                              return { ...prev, behaviorSymptoms: Array.from(symptoms) }
                            })
                          }}
                          className="rounded border-gray-300 text-blue-600 focus:ring-2 focus:ring-blue-500"
                        />
                        <span className="text-sm text-gray-700 dark:text-gray-300">{symptom}</span>
                      </label>
                    ))}
                  </div>
                ) : (
                  <Input
                    id="description"
                    value={newRecord.description}
                    onChange={(e) => setNewRecord({ ...newRecord, description: e.target.value })}
                    className="col-span-3"
                    placeholder="Brief description of the record"
                  />
                )}
              </div>
              <div className="grid grid-cols-4 items-center gap-4">
                <Label htmlFor="veterinarian" className="text-right">
                  Veterinarian
                </Label>
                <Input
                  id="veterinarian"
                  value={newRecord.veterinarian}
                  onChange={(e) => setNewRecord({ ...newRecord, veterinarian: e.target.value })}
                  className="col-span-3"
                  placeholder="Dr. Name (optional)"
                />
              </div>
              <div className="grid grid-cols-4 items-center gap-4">
                <Label htmlFor="notes" className="text-right">
                  Notes
                </Label>
                <Textarea
                  id="notes"
                  value={newRecord.notes}
                  onChange={(e) => setNewRecord({ ...newRecord, notes: e.target.value })}
                  className="col-span-3"
                  placeholder="Additional notes (optional)"
                />
              </div>
            </div>
            <div className="flex justify-end space-x-2">
              <Button variant="outline" onClick={() => setIsAddingRecord(false)}>
                Cancel
              </Button>
              <Button onClick={handleAddRecord}>Add Record</Button>
            </div>
          </DialogContent>
        </Dialog>
      </div>

      {/* Health Records List */}
      <div className="space-y-4">
        {sortedRecords.length === 0 ? (
          <Card>
            <CardContent className="text-center py-8">
              <p className="text-gray-500 dark:text-gray-400">
                No health records found. Add your first record to get started.
              </p>
            </CardContent>
          </Card>
        ) : (
          sortedRecords.map((record) => (
            <Card key={record.id}>
              <CardHeader>
                <div className="flex items-center justify-between">
                  <CardTitle className="text-lg">
                    {record.description}
                  </CardTitle>
                  <div className="flex items-center space-x-2">
                    <Badge className={getTypeColor(record.type)}>
                      {record.type.charAt(0).toUpperCase() + record.type.slice(1)}
                    </Badge>
                    <span className="text-sm text-gray-500 dark:text-gray-400">
                    {new Date(record.date).toLocaleDateString('en-IN')}
                    </span>
                  </div>
                </div>
                {record.veterinarian && (
                  <CardDescription>
                    Veterinarian: {record.veterinarian}
                  </CardDescription>
                )}
              </CardHeader>
              {record.notes && (
                <CardContent>
                  <p className="text-gray-600 dark:text-gray-400">
                    {record.notes}
                  </p>
                </CardContent>
              )}
            </Card>
          ))
        )}
      </div>

      {/* Health Summary */}
      <Card>
        <CardHeader>
          <CardTitle>Health Summary</CardTitle>
          <CardDescription>Overview of health record types</CardDescription>
        </CardHeader>
        <CardContent>
          <div className="grid grid-cols-2 md:grid-cols-4 gap-4">
            {['checkup', 'illness', 'behavior', 'injury'].map((type) => {
              const count = petData.healthRecords.filter(r => r.type === type).length
              return (
                <div key={type} className="text-center p-4 bg-gray-50 dark:bg-gray-800 rounded-lg">
                  <div className="text-2xl font-bold text-gray-900 dark:text-white">
                    {count}
                  </div>
                  <div className="text-sm text-gray-600 dark:text-gray-400 capitalize">
                    {type} Records
                  </div>
                </div>
              )
            })}
          </div>
        </CardContent>
      </Card>
    </div>
  )
}
