'use client'

import { useState } from 'react'
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/card'
import { Button } from '@/components/ui/button'
import { Input } from '@/components/ui/input'
import { Textarea } from '@/components/ui/textarea'
import { Label } from '@/components/ui/label'

export default function CustomerService() {
  // Form state
  const [form, setForm] = useState({
    name: '',
    email: '',
    subject: '',
    message: ''
  })
  const [loading, setLoading] = useState(false)
  const [error, setError] = useState('')
  const [success, setSuccess] = useState('')

  // Update form values
  const handleChange = (e: React.ChangeEvent<HTMLInputElement | HTMLTextAreaElement>) => {
    setForm({ ...form, [e.target.name]: e.target.value })
  }

  // Validate form fields and submit the form
  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault()
    setError('')
    setSuccess('')

    // Simple validation
    if (!form.name || !form.email || !form.subject || !form.message) {
      setError('All fields are required.')
      return
    }

    // Email validation
    const emailRegex = /^[^\s@]+@[^\s@]+\.[^\s@]+$/
    if (!emailRegex.test(form.email)) {
      setError('Please enter a valid email address.')
      return
    }

    setLoading(true)
    try {
      const res = await fetch('/api/support', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json'
        },
        body: JSON.stringify(form)
      })
      
      if (!res.ok) {
        throw new Error('Failed to send message. Please try again later.')
      }
      
      setSuccess('Your message has been sent successfully! We will get back to you within 24 hours.')
      // Clear form after successful submission
      setForm({ name: '', email: '', subject: '', message: '' })
    } catch (err: any) {
      setError(err.message || 'An error occurred. Please try again.')
    } finally {
      setLoading(false)
    }
  }

  // Dummy data for nearby veterinary clinics
  const dummyClinics = [
    {
      name: 'Happy Paws Veterinary Clinic',
      address: '123 Pet Street, Cityville, CA 90210',
      phone: '(555) 123-4567',
      distance: '1.2 miles',
      hours: 'Mon-Fri: 8AM-6PM, Sat: 9AM-4PM',
      specialties: ['General Care', 'Surgery', 'Emergency']
    },
    {
      name: 'Animal Care Center',
      address: '456 Paw Avenue, Townsville, CA 90211',
      phone: '(555) 567-8901',
      distance: '2.5 miles',
      hours: 'Mon-Sun: 7AM-8PM',
      specialties: ['24/7 Emergency', 'Dental', 'Cardiology']
    },
    {
      name: 'Healthy Tails Veterinary Hospital',
      address: '789 Furry Road, Village, CA 90212',
      phone: '(555) 901-2345',
      distance: '3.0 miles',
      hours: 'Mon-Fri: 7AM-7PM, Weekends: 8AM-5PM',
      specialties: ['Exotic Pets', 'Oncology', 'Rehabilitation']
    },
    {
      name: 'Pet Wellness Clinic',
      address: '321 Animal Lane, Pettown, CA 90213',
      phone: '(555) 234-5678',
      distance: '3.8 miles',
      hours: 'Mon-Sat: 8AM-6PM',
      specialties: ['Preventive Care', 'Grooming', 'Nutrition']
    }
  ]

  return (
    <div className="space-y-8">
      <div className="mb-6">
        <h1 className="text-3xl font-bold text-gray-900 dark:text-white mb-2">Customer Service</h1>
        <p className="text-gray-600 dark:text-gray-400">
          Get help with your Pet ProTech device or find nearby veterinary clinics for your pet's care.
        </p>
      </div>

      <div className="grid lg:grid-cols-2 gap-8">
        {/* Contact Support Form */}
        <Card>
          <CardHeader>
            <CardTitle className="text-xl">Contact Support</CardTitle>
            <CardDescription>
              Having issues with your Pet ProTech device? Send us a message and we'll help you out.
            </CardDescription>
          </CardHeader>
          <CardContent>
            {error && (
              <div className="mb-4 p-3 bg-red-50 dark:bg-red-900/20 border border-red-200 dark:border-red-800 rounded-md">
                <p className="text-red-600 dark:text-red-400 text-sm">{error}</p>
              </div>
            )}
            {success && (
              <div className="mb-4 p-3 bg-green-50 dark:bg-green-900/20 border border-green-200 dark:border-green-800 rounded-md">
                <p className="text-green-600 dark:text-green-400 text-sm">{success}</p>
              </div>
            )}
            <form onSubmit={handleSubmit} className="space-y-4">
              <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
                <div>
                  <Label htmlFor="name">Full Name</Label>
                  <Input
                    id="name"
                    name="name"
                    type="text"
                    value={form.name}
                    onChange={handleChange}
                    placeholder="Your full name"
                    className="mt-1"
                  />
                </div>
                <div>
                  <Label htmlFor="email">Email Address</Label>
                  <Input
                    id="email"
                    name="email"
                    type="email"
                    value={form.email}
                    onChange={handleChange}
                    placeholder="you@example.com"
                    className="mt-1"
                  />
                </div>
              </div>
              <div>
                <Label htmlFor="subject">Subject</Label>
                <Input
                  id="subject"
                  name="subject"
                  type="text"
                  value={form.subject}
                  onChange={handleChange}
                  placeholder="Brief description of your issue"
                  className="mt-1"
                />
              </div>
              <div>
                <Label htmlFor="message">Message</Label>
                <Textarea
                  id="message"
                  name="message"
                  value={form.message}
                  onChange={handleChange}
                  placeholder="Please describe your issue or question in detail..."
                  className="mt-1 min-h-[120px]"
                />
              </div>
              <Button
                type="submit"
                disabled={loading}
                className="w-full"
              >
                {loading ? 'Sending Message...' : 'Send Message'}
              </Button>
            </form>
          </CardContent>
        </Card>

        {/* Quick Help Section */}
        <Card>
          <CardHeader>
            <CardTitle className="text-xl">Quick Help</CardTitle>
            <CardDescription>
              Common questions and helpful resources
            </CardDescription>
          </CardHeader>
          <CardContent className="space-y-4">
            <div className="space-y-3">
              <div className="p-3 bg-blue-50 dark:bg-blue-900/20 rounded-lg">
                <h4 className="font-medium text-blue-900 dark:text-blue-100 mb-1">Device Setup</h4>
                <p className="text-sm text-blue-700 dark:text-blue-300">
                  Need help setting up your Pet ProTech device? Check our setup guide or contact support.
                </p>
              </div>
              <div className="p-3 bg-green-50 dark:bg-green-900/20 rounded-lg">
                <h4 className="font-medium text-green-900 dark:text-green-100 mb-1">Health Monitoring</h4>
                <p className="text-sm text-green-700 dark:text-green-300">
                  Questions about health readings or alerts? Our support team can help interpret the data.
                </p>
              </div>
              <div className="p-3 bg-purple-50 dark:bg-purple-900/20 rounded-lg">
                <h4 className="font-medium text-purple-900 dark:text-purple-100 mb-1">Technical Issues</h4>
                <p className="text-sm text-purple-700 dark:text-purple-300">
                  Device not working properly? We're here to troubleshoot and resolve any technical problems.
                </p>
              </div>
            </div>
            <div className="pt-4 border-t border-gray-200 dark:border-gray-700">
              <p className="text-sm text-gray-600 dark:text-gray-400 mb-2">
                <strong>Support Hours:</strong> Monday - Friday, 8AM - 6PM PST
              </p>
              <p className="text-sm text-gray-600 dark:text-gray-400">
                <strong>Emergency Support:</strong> Available 24/7 for critical pet health alerts
              </p>
            </div>
          </CardContent>
        </Card>
      </div>

      {/* Nearby Veterinary Clinics Section */}
      <Card>
        <CardHeader>
          <CardTitle className="text-xl">Nearby Veterinary Clinics</CardTitle>
          <CardDescription>
            Find trusted veterinary care near you for your pet's health needs
          </CardDescription>
        </CardHeader>
        <CardContent>
          <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
            {dummyClinics.map((clinic, index) => (
              <div key={index} className="p-4 border border-gray-200 dark:border-gray-700 rounded-lg hover:shadow-md transition-shadow">
                <div className="flex justify-between items-start mb-3">
                  <h3 className="text-lg font-semibold text-gray-900 dark:text-white">{clinic.name}</h3>
                  <span className="text-sm text-blue-600 dark:text-blue-400 font-medium">{clinic.distance}</span>
                </div>
                <div className="space-y-2 text-sm text-gray-600 dark:text-gray-400">
                  <p className="flex items-start">
                    <span className="font-medium mr-2">Address:</span>
                    {clinic.address}
                  </p>
                  <p className="flex items-center">
                    <span className="font-medium mr-2">Phone:</span>
                    <a href={`tel:${clinic.phone}`} className="text-blue-600 dark:text-blue-400 hover:underline">
                      {clinic.phone}
                    </a>
                  </p>
                  <p className="flex items-start">
                    <span className="font-medium mr-2">Hours:</span>
                    {clinic.hours}
                  </p>
                  <div className="flex items-start">
                    <span className="font-medium mr-2">Specialties:</span>
                    <div className="flex flex-wrap gap-1">
                      {clinic.specialties.map((specialty, idx) => (
                        <span key={idx} className="px-2 py-1 bg-gray-100 dark:bg-gray-700 text-xs rounded-full">
                          {specialty}
                        </span>
                      ))}
                    </div>
                  </div>
                </div>
                <div className="mt-4 flex gap-2">
                  <Button variant="outline" size="sm" className="flex-1">
                    Call Now
                  </Button>
                  <Button variant="outline" size="sm" className="flex-1">
                    Get Directions
                  </Button>
                </div>
              </div>
            ))}
          </div>
        </CardContent>
      </Card>
    </div>
  )
}
