'use client'

interface SidebarProps {
  activeSection: string
  onSectionChange: (section: 'dashboard' | 'health' | 'activities' | 'medications' | 'orderMedicine' | 'behaviorChanges' | 'vaccinations' | 'insurance' | 'profile' | 'support') => void
}

export default function Sidebar({ activeSection, onSectionChange }: SidebarProps) {
  const menuItems = [
    { id: 'dashboard', label: 'Dashboard', description: 'Overview & Live Data' },
    { id: 'health', label: 'Health Records', description: 'Medical History' },
    { id: 'activities', label: 'Activities', description: 'Daily Activity Log' },
    { id: 'medications', label: 'Medications', description: 'Current Prescriptions' },
    { id: 'orderMedicine', label: 'Order Medicine', description: 'Order Prescribed Medicine' },
    { id: 'behaviorChanges', label: 'Behavior Changes', description: 'Behavioral Change Summary' },
    { id: 'vaccinations', label: 'Vaccinations', description: 'Immunization Records' },
    { id: 'insurance', label: 'Insurance', description: 'Policy Details' },
    { id: 'profile', label: 'Pet Profile', description: 'Basic Information' },
    { id: 'support', label: 'Customer Service', description: 'Contact support & find nearby veterinary clinics' },
  ]

  return (
    <aside className="w-64 bg-white dark:bg-gray-800 shadow-sm border-r border-gray-200 dark:border-gray-700 min-h-screen">
      <nav className="p-4">
        <div className="space-y-2">
          {menuItems.map((item) => (
            <button
              key={item.id}
              onClick={() => onSectionChange(item.id as any)}
              className={`w-full text-left p-3 rounded-lg transition-colors duration-200 ${
                activeSection === item.id
                  ? 'bg-blue-50 dark:bg-blue-900/20 text-blue-700 dark:text-blue-300 border border-blue-200 dark:border-blue-800'
                  : 'text-gray-700 dark:text-gray-300 hover:bg-gray-50 dark:hover:bg-gray-700'
              }`}
            >
              <div className="font-medium text-sm">{item.label}</div>
              <div className="text-xs text-gray-500 dark:text-gray-400 mt-1">
                {item.description}
              </div>
            </button>
          ))}
        </div>
      </nav>
    </aside>
  )
}
