'use client'

import { useState } from 'react'
import { usePetData } from '@/context/PetDataContext'
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/card'
import { Button } from '@/components/ui/button'
import { Input } from '@/components/ui/input'
import { Label } from '@/components/ui/label'
import { Textarea } from '@/components/ui/textarea'
import { Badge } from '@/components/ui/badge'
import { Switch } from '@/components/ui/switch'
import { Dialog, DialogContent, DialogDescription, DialogHeader, DialogTitle, DialogTrigger } from '@/components/ui/dialog'

export default function Medications() {
  const { petData, dispatch } = usePetData()
  const [isAddingMedication, setIsAddingMedication] = useState(false)
  const [newMedication, setNewMedication] = useState({
    name: '',
    dosage: '',
    frequency: '',
    startDate: new Date().toISOString().split('T')[0],
    endDate: '',
    prescribedBy: '',
    notes: '',
    active: true
  })

  const handleAddMedication = () => {
    if (newMedication.name.trim() && newMedication.dosage.trim() && newMedication.frequency.trim()) {
      dispatch({
        type: 'ADD_MEDICATION',
        payload: {
          id: Date.now().toString(),
          ...newMedication
        }
      })
      setNewMedication({
        name: '',
        dosage: '',
        frequency: '',
        startDate: new Date().toISOString().split('T')[0],
        endDate: '',
        prescribedBy: '',
        notes: '',
        active: true
      })
      setIsAddingMedication(false)
    }
  }

  const toggleMedicationStatus = (id: string, active: boolean) => {
    dispatch({
      type: 'UPDATE_MEDICATION',
      payload: {
        id,
        updates: { active }
      }
    })
  }

  const activeMedications = petData.medications.filter(med => med.active)
  const inactiveMedications = petData.medications.filter(med => !med.active)

  const isExpiringSoon = (medication: any) => {
    if (!medication.endDate) return false
    const endDate = new Date(medication.endDate)
    const today = new Date()
    const daysUntilExpiry = Math.ceil((endDate.getTime() - today.getTime()) / (1000 * 60 * 60 * 24))
    return daysUntilExpiry <= 7 && daysUntilExpiry > 0
  }

  const isExpired = (medication: any) => {
    if (!medication.endDate) return false
    const endDate = new Date(medication.endDate)
    const today = new Date()
    return endDate < today
  }

  return (
    <div className="space-y-6">
      <div className="flex items-center justify-between">
        <div>
          <h1 className="text-3xl font-bold text-gray-900 dark:text-white">
            Medications
          </h1>
          <p className="text-gray-600 dark:text-gray-400 mt-1">
            Manage {petData.pet.name}'s current and past medications
          </p>
        </div>
        <Dialog open={isAddingMedication} onOpenChange={setIsAddingMedication}>
          <DialogTrigger asChild>
            <Button>Add Medication</Button>
          </DialogTrigger>
          <DialogContent className="sm:max-w-[425px]">
            <DialogHeader>
              <DialogTitle>Add Medication</DialogTitle>
              <DialogDescription>
                Add a new medication for {petData.pet.name}.
              </DialogDescription>
            </DialogHeader>
            <div className="grid gap-4 py-4">
              <div className="grid grid-cols-4 items-center gap-4">
                <Label htmlFor="name" className="text-right">
                  Name
                </Label>
                <Input
                  id="name"
                  value={newMedication.name}
                  onChange={(e) => setNewMedication({ ...newMedication, name: e.target.value })}
                  className="col-span-3"
                  placeholder="Medication name"
                />
              </div>
              <div className="grid grid-cols-4 items-center gap-4">
                <Label htmlFor="dosage" className="text-right">
                  Dosage
                </Label>
                <Input
                  id="dosage"
                  value={newMedication.dosage}
                  onChange={(e) => setNewMedication({ ...newMedication, dosage: e.target.value })}
                  className="col-span-3"
                  placeholder="e.g., 1 tablet, 5ml"
                />
              </div>
              <div className="grid grid-cols-4 items-center gap-4">
                <Label htmlFor="frequency" className="text-right">
                  Frequency
                </Label>
                <Input
                  id="frequency"
                  value={newMedication.frequency}
                  onChange={(e) => setNewMedication({ ...newMedication, frequency: e.target.value })}
                  className="col-span-3"
                  placeholder="e.g., Twice daily, Weekly"
                />
              </div>
              <div className="grid grid-cols-4 items-center gap-4">
                <Label htmlFor="startDate" className="text-right">
                  Start Date
                </Label>
                <Input
                  id="startDate"
                  type="date"
                  value={newMedication.startDate}
                  onChange={(e) => setNewMedication({ ...newMedication, startDate: e.target.value })}
                  className="col-span-3"
                />
              </div>
              <div className="grid grid-cols-4 items-center gap-4">
                <Label htmlFor="endDate" className="text-right">
                  End Date
                </Label>
                <Input
                  id="endDate"
                  type="date"
                  value={newMedication.endDate}
                  onChange={(e) => setNewMedication({ ...newMedication, endDate: e.target.value })}
                  className="col-span-3"
                  placeholder="Optional"
                />
              </div>
              <div className="grid grid-cols-4 items-center gap-4">
                <Label htmlFor="prescribedBy" className="text-right">
                  Prescribed By
                </Label>
                <Input
                  id="prescribedBy"
                  value={newMedication.prescribedBy}
                  onChange={(e) => setNewMedication({ ...newMedication, prescribedBy: e.target.value })}
                  className="col-span-3"
                  placeholder="Dr. Name"
                />
              </div>
              <div className="grid grid-cols-4 items-center gap-4">
                <Label htmlFor="notes" className="text-right">
                  Notes
                </Label>
                <Textarea
                  id="notes"
                  value={newMedication.notes}
                  onChange={(e) => setNewMedication({ ...newMedication, notes: e.target.value })}
                  className="col-span-3"
                  placeholder="Additional instructions (optional)"
                />
              </div>
            </div>
            <div className="flex justify-end space-x-2">
              <Button variant="outline" onClick={() => setIsAddingMedication(false)}>
                Cancel
              </Button>
              <Button onClick={handleAddMedication}>Add Medication</Button>
            </div>
          </DialogContent>
        </Dialog>
      </div>

      {/* Medication Summary */}
      <Card>
        <CardHeader>
          <CardTitle>Medication Summary</CardTitle>
          <CardDescription>Current medication status overview</CardDescription>
        </CardHeader>
        <CardContent>
          <div className="grid grid-cols-2 md:grid-cols-4 gap-4">
            <div className="text-center p-4 bg-green-50 dark:bg-green-900/20 rounded-lg">
              <div className="text-2xl font-bold text-green-600 dark:text-green-400">
                {activeMedications.length}
              </div>
              <div className="text-sm text-gray-600 dark:text-gray-400">
                Active Medications
              </div>
            </div>
            <div className="text-center p-4 bg-yellow-50 dark:bg-yellow-900/20 rounded-lg">
              <div className="text-2xl font-bold text-yellow-600 dark:text-yellow-400">
                {activeMedications.filter(med => isExpiringSoon(med)).length}
              </div>
              <div className="text-sm text-gray-600 dark:text-gray-400">
                Expiring Soon
              </div>
            </div>
            <div className="text-center p-4 bg-red-50 dark:bg-red-900/20 rounded-lg">
              <div className="text-2xl font-bold text-red-600 dark:text-red-400">
                {activeMedications.filter(med => isExpired(med)).length}
              </div>
              <div className="text-sm text-gray-600 dark:text-gray-400">
                Expired
              </div>
            </div>
            <div className="text-center p-4 bg-gray-50 dark:bg-gray-800 rounded-lg">
              <div className="text-2xl font-bold text-gray-600 dark:text-gray-400">
                {inactiveMedications.length}
              </div>
              <div className="text-sm text-gray-600 dark:text-gray-400">
                Inactive
              </div>
            </div>
          </div>
        </CardContent>
      </Card>

      {/* Active Medications */}
      <div className="space-y-4">
        <h2 className="text-xl font-semibold text-gray-900 dark:text-white">
          Active Medications
        </h2>
        {activeMedications.length === 0 ? (
          <Card>
            <CardContent className="text-center py-8">
              <p className="text-gray-500 dark:text-gray-400">
                No active medications. Add a medication to get started.
              </p>
            </CardContent>
          </Card>
        ) : (
          activeMedications.map((medication) => (
            <Card key={medication.id}>
              <CardHeader>
                <div className="flex items-center justify-between">
                  <CardTitle className="text-lg flex items-center space-x-2">
                    <span>{medication.name}</span>
                    {isExpired(medication) && (
                      <Badge variant="destructive">Expired</Badge>
                    )}
                    {isExpiringSoon(medication) && !isExpired(medication) && (
                      <Badge className="bg-yellow-500 text-white">Expiring Soon</Badge>
                    )}
                  </CardTitle>
                  <div className="flex items-center space-x-2">
                    <Label htmlFor={`active-${medication.id}`} className="text-sm">
                      Active
                    </Label>
                    <Switch
                      id={`active-${medication.id}`}
                      checked={medication.active}
                      onCheckedChange={(checked) => toggleMedicationStatus(medication.id, checked)}
                    />
                  </div>
                </div>
                <CardDescription>
                  {medication.dosage} - {medication.frequency}
                  {medication.prescribedBy && ` â¢ Prescribed by ${medication.prescribedBy}`}
                </CardDescription>
              </CardHeader>
              <CardContent>
                <div className="grid md:grid-cols-2 gap-4">
                  <div>
                    <p className="text-sm text-gray-600 dark:text-gray-400">
                      <strong>Start Date:</strong> {new Date(medication.startDate).toLocaleDateString()}
                    </p>
                    {medication.endDate && (
                      <p className="text-sm text-gray-600 dark:text-gray-400">
                        <strong>End Date:</strong> {new Date(medication.endDate).toLocaleDateString()}
                      </p>
                    )}
                  </div>
                  {medication.notes && (
                    <div>
                      <p className="text-sm text-gray-600 dark:text-gray-400">
                        <strong>Notes:</strong> {medication.notes}
                      </p>
                    </div>
                  )}
                </div>
              </CardContent>
            </Card>
          ))
        )}
      </div>

      {/* Inactive Medications */}
      {inactiveMedications.length > 0 && (
        <div className="space-y-4">
          <h2 className="text-xl font-semibold text-gray-900 dark:text-white">
            Inactive Medications
          </h2>
          {inactiveMedications.map((medication) => (
            <Card key={medication.id} className="opacity-75">
              <CardHeader>
                <div className="flex items-center justify-between">
                  <CardTitle className="text-lg flex items-center space-x-2">
                    <span>{medication.name}</span>
                    <Badge variant="secondary">Inactive</Badge>
                  </CardTitle>
                  <div className="flex items-center space-x-2">
                    <Label htmlFor={`active-${medication.id}`} className="text-sm">
                      Active
                    </Label>
                    <Switch
                      id={`active-${medication.id}`}
                      checked={medication.active}
                      onCheckedChange={(checked) => toggleMedicationStatus(medication.id, checked)}
                    />
                  </div>
                </div>
                <CardDescription>
                  {medication.dosage} - {medication.frequency}
                  {medication.prescribedBy && ` â¢ Prescribed by ${medication.prescribedBy}`}
                </CardDescription>
              </CardHeader>
            </Card>
          ))}
        </div>
      )}
    </div>
  )
}
