'use client'

import { useState } from 'react'
import { usePetData } from '@/context/PetDataContext'
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/card'
import { Button } from '@/components/ui/button'
import { Input } from '@/components/ui/input'
import { Label } from '@/components/ui/label'
import { Textarea } from '@/components/ui/textarea'
import { Badge } from '@/components/ui/badge'
import { Dialog, DialogContent, DialogDescription, DialogHeader, DialogTitle, DialogTrigger } from '@/components/ui/dialog'

export default function Vaccinations() {
  const { petData, dispatch } = usePetData()
  const [isAddingVaccination, setIsAddingVaccination] = useState(false)
  const [newVaccination, setNewVaccination] = useState({
    vaccineName: '',
    dateGiven: new Date().toISOString().split('T')[0],
    nextDue: '',
    veterinarian: '',
    batchNumber: '',
    notes: ''
  })

  const handleAddVaccination = () => {
    if (newVaccination.vaccineName.trim() && newVaccination.dateGiven) {
      dispatch({
        type: 'ADD_VACCINATION',
        payload: {
          id: Date.now().toString(),
          ...newVaccination
        }
      })
      setNewVaccination({
        vaccineName: '',
        dateGiven: new Date().toISOString().split('T')[0],
        nextDue: '',
        veterinarian: '',
        batchNumber: '',
        notes: ''
      })
      setIsAddingVaccination(false)
    }
  }

  const isDueForVaccination = (vaccination: any) => {
    if (!vaccination.nextDue) return false
    const dueDate = new Date(vaccination.nextDue)
    const today = new Date()
    const daysUntilDue = Math.ceil((dueDate.getTime() - today.getTime()) / (1000 * 60 * 60 * 24))
    return daysUntilDue <= 30 && daysUntilDue > 0
  }

  const isOverdue = (vaccination: any) => {
    if (!vaccination.nextDue) return false
    const dueDate = new Date(vaccination.nextDue)
    const today = new Date()
    return dueDate < today
  }

  const sortedVaccinations = [...petData.vaccinations].sort((a, b) => 
    new Date(b.dateGiven).getTime() - new Date(a.dateGiven).getTime()
  )

  const upcomingVaccinations = sortedVaccinations.filter(v => isDueForVaccination(v))
  const overdueVaccinations = sortedVaccinations.filter(v => isOverdue(v))

  return (
    <div className="space-y-6">
      <div className="flex items-center justify-between">
        <div>
          <h1 className="text-3xl font-bold text-gray-900 dark:text-white">
            Vaccinations
          </h1>
          <p className="text-gray-600 dark:text-gray-400 mt-1">
            Track {petData.pet.name}'s vaccination history and upcoming reminders
          </p>
        </div>
        <Dialog open={isAddingVaccination} onOpenChange={setIsAddingVaccination}>
          <DialogTrigger asChild>
            <Button>Add Vaccination</Button>
          </DialogTrigger>
          <DialogContent className="sm:max-w-[425px]">
            <DialogHeader>
              <DialogTitle>Add Vaccination Record</DialogTitle>
              <DialogDescription>
                Record a vaccination for {petData.pet.name}.
              </DialogDescription>
            </DialogHeader>
            <div className="grid gap-4 py-4">
              <div className="grid grid-cols-4 items-center gap-4">
                <Label htmlFor="vaccineName" className="text-right">
                  Vaccine Name
                </Label>
                <Input
                  id="vaccineName"
                  value={newVaccination.vaccineName}
                  onChange={(e) => setNewVaccination({ ...newVaccination, vaccineName: e.target.value })}
                  className="col-span-3"
                  placeholder="e.g., DHPP, Rabies"
                />
              </div>
              <div className="grid grid-cols-4 items-center gap-4">
                <Label htmlFor="dateGiven" className="text-right">
                  Date Given
                </Label>
                <Input
                  id="dateGiven"
                  type="date"
                  value={newVaccination.dateGiven}
                  onChange={(e) => setNewVaccination({ ...newVaccination, dateGiven: e.target.value })}
                  className="col-span-3"
                />
              </div>
              <div className="grid grid-cols-4 items-center gap-4">
                <Label htmlFor="nextDue" className="text-right">
                  Next Due
                </Label>
                <Input
                  id="nextDue"
                  type="date"
                  value={newVaccination.nextDue}
                  onChange={(e) => setNewVaccination({ ...newVaccination, nextDue: e.target.value })}
                  className="col-span-3"
                  placeholder="Optional"
                />
              </div>
              <div className="grid grid-cols-4 items-center gap-4">
                <Label htmlFor="veterinarian" className="text-right">
                  Veterinarian
                </Label>
                <Input
                  id="veterinarian"
                  value={newVaccination.veterinarian}
                  onChange={(e) => setNewVaccination({ ...newVaccination, veterinarian: e.target.value })}
                  className="col-span-3"
                  placeholder="Dr. Name"
                />
              </div>
              <div className="grid grid-cols-4 items-center gap-4">
                <Label htmlFor="batchNumber" className="text-right">
                  Batch Number
                </Label>
                <Input
                  id="batchNumber"
                  value={newVaccination.batchNumber}
                  onChange={(e) => setNewVaccination({ ...newVaccination, batchNumber: e.target.value })}
                  className="col-span-3"
                  placeholder="Optional"
                />
              </div>
              <div className="grid grid-cols-4 items-center gap-4">
                <Label htmlFor="notes" className="text-right">
                  Notes
                </Label>
                <Textarea
                  id="notes"
                  value={newVaccination.notes}
                  onChange={(e) => setNewVaccination({ ...newVaccination, notes: e.target.value })}
                  className="col-span-3"
                  placeholder="Additional notes (optional)"
                />
              </div>
            </div>
            <div className="flex justify-end space-x-2">
              <Button variant="outline" onClick={() => setIsAddingVaccination(false)}>
                Cancel
              </Button>
              <Button onClick={handleAddVaccination}>Add Vaccination</Button>
            </div>
          </DialogContent>
        </Dialog>
      </div>

      {/* Vaccination Status Summary */}
      <Card>
        <CardHeader>
          <CardTitle>Vaccination Status</CardTitle>
          <CardDescription>Current vaccination status overview</CardDescription>
        </CardHeader>
        <CardContent>
          <div className="grid grid-cols-2 md:grid-cols-4 gap-4">
            <div className="text-center p-4 bg-green-50 dark:bg-green-900/20 rounded-lg">
              <div className="text-2xl font-bold text-green-600 dark:text-green-400">
                {petData.vaccinations.length}
              </div>
              <div className="text-sm text-gray-600 dark:text-gray-400">
                Total Vaccinations
              </div>
            </div>
            <div className="text-center p-4 bg-blue-50 dark:bg-blue-900/20 rounded-lg">
              <div className="text-2xl font-bold text-blue-600 dark:text-blue-400">
                {petData.vaccinations.filter(v => v.nextDue && new Date(v.nextDue) > new Date()).length}
              </div>
              <div className="text-sm text-gray-600 dark:text-gray-400">
                Up to Date
              </div>
            </div>
            <div className="text-center p-4 bg-yellow-50 dark:bg-yellow-900/20 rounded-lg">
              <div className="text-2xl font-bold text-yellow-600 dark:text-yellow-400">
                {upcomingVaccinations.length}
              </div>
              <div className="text-sm text-gray-600 dark:text-gray-400">
                Due Soon
              </div>
            </div>
            <div className="text-center p-4 bg-red-50 dark:bg-red-900/20 rounded-lg">
              <div className="text-2xl font-bold text-red-600 dark:text-red-400">
                {overdueVaccinations.length}
              </div>
              <div className="text-sm text-gray-600 dark:text-gray-400">
                Overdue
              </div>
            </div>
          </div>
        </CardContent>
      </Card>

      {/* Overdue Vaccinations Alert */}
      {overdueVaccinations.length > 0 && (
        <Card className="border-red-200 dark:border-red-800">
          <CardHeader>
            <CardTitle className="text-red-600 dark:text-red-400">
              Overdue Vaccinations
            </CardTitle>
            <CardDescription>
              These vaccinations are past their due date and need immediate attention.
            </CardDescription>
          </CardHeader>
          <CardContent>
            <div className="space-y-2">
              {overdueVaccinations.map((vaccination) => (
                <div key={vaccination.id} className="flex items-center justify-between p-3 bg-red-50 dark:bg-red-900/20 rounded-lg">
                  <div>
                    <p className="font-medium text-red-800 dark:text-red-200">
                      {vaccination.vaccineName}
                    </p>
                      <p className="text-sm text-red-600 dark:text-red-400">
                        Due: {vaccination.nextDue ? new Date(vaccination.nextDue).toLocaleDateString('en-IN') : 'Not specified'}
                      </p>
                  </div>
                  <Badge variant="destructive">Overdue</Badge>
                </div>
              ))}
            </div>
          </CardContent>
        </Card>
      )}

      {/* Upcoming Vaccinations */}
      {upcomingVaccinations.length > 0 && (
        <Card className="border-yellow-200 dark:border-yellow-800">
          <CardHeader>
            <CardTitle className="text-yellow-600 dark:text-yellow-400">
              Upcoming Vaccinations
            </CardTitle>
            <CardDescription>
              These vaccinations are due within the next 30 days.
            </CardDescription>
          </CardHeader>
          <CardContent>
            <div className="space-y-2">
              {upcomingVaccinations.map((vaccination) => (
                <div key={vaccination.id} className="flex items-center justify-between p-3 bg-yellow-50 dark:bg-yellow-900/20 rounded-lg">
                  <div>
                    <p className="font-medium text-yellow-800 dark:text-yellow-200">
                      {vaccination.vaccineName}
                    </p>
                      <p className="text-sm text-yellow-600 dark:text-yellow-400">
                        Due: {vaccination.nextDue ? new Date(vaccination.nextDue).toLocaleDateString('en-IN') : 'Not specified'}
                      </p>
                  </div>
                  <Badge className="bg-yellow-500 text-white">Due Soon</Badge>
                </div>
              ))}
            </div>
          </CardContent>
        </Card>
      )}

      {/* Vaccination History */}
      <div className="space-y-4">
        <h2 className="text-xl font-semibold text-gray-900 dark:text-white">
          Vaccination History
        </h2>
        {sortedVaccinations.length === 0 ? (
          <Card>
            <CardContent className="text-center py-8">
              <p className="text-gray-500 dark:text-gray-400">
                No vaccination records found. Add the first vaccination record to get started.
              </p>
            </CardContent>
          </Card>
        ) : (
          sortedVaccinations.map((vaccination) => (
            <Card key={vaccination.id}>
              <CardHeader>
                <div className="flex items-center justify-between">
                  <CardTitle className="text-lg flex items-center space-x-2">
                    <span>{vaccination.vaccineName}</span>
                    {isOverdue(vaccination) && (
                      <Badge variant="destructive">Overdue</Badge>
                    )}
                    {isDueForVaccination(vaccination) && !isOverdue(vaccination) && (
                      <Badge className="bg-yellow-500 text-white">Due Soon</Badge>
                    )}
                    {vaccination.nextDue && new Date(vaccination.nextDue) > new Date() && 
                     !isDueForVaccination(vaccination) && (
                      <Badge className="bg-green-500 text-white">Up to Date</Badge>
                    )}
                  </CardTitle>
                  <span className="text-sm text-gray-500 dark:text-gray-400">
                    Given: {new Date(vaccination.dateGiven).toLocaleDateString('en-IN')}
                  </span>
                </div>
                <CardDescription>
                  {vaccination.veterinarian && `Administered by ${vaccination.veterinarian}`}
                  {vaccination.batchNumber && ` â¢ Batch: ${vaccination.batchNumber}`}
                </CardDescription>
              </CardHeader>
              <CardContent>
                <div className="grid md:grid-cols-2 gap-4">
                  <div>
                    {vaccination.nextDue && (
                      <p className="text-sm text-gray-600 dark:text-gray-400">
                        <strong>Next Due:</strong> {new Date(vaccination.nextDue).toLocaleDateString('en-IN')}
                      </p>
                    )}
                  </div>
                  {vaccination.notes && (
                    <div>
                      <p className="text-sm text-gray-600 dark:text-gray-400">
                        <strong>Notes:</strong> {vaccination.notes}
                      </p>
                    </div>
                  )}
                </div>
              </CardContent>
            </Card>
          ))
        )}
      </div>
    </div>
  )
}
