'use client'

import { usePetData } from '@/context/PetDataContext'
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card'

export default function BehaviorChangesDashboard() {
  const { petData } = usePetData()

  // Filter behavior type health records
  const behaviorRecords = petData.healthRecords.filter(record => record.type === 'behavior')

  // Count occurrences of each symptom
  const symptomCounts: Record<string, number> = {}

  behaviorRecords.forEach(record => {
    const symptoms = record.description.split(',').map(s => s.trim())
    symptoms.forEach(symptom => {
      if (symptom) {
        symptomCounts[symptom] = (symptomCounts[symptom] || 0) + 1
      }
    })
  })

  const symptoms = Object.entries(symptomCounts).sort((a, b) => b[1] - a[1])

  return (
    <div className="space-y-6">
      <h1 className="text-3xl font-bold text-gray-900 dark:text-white">
        Behavior Changes Summary
      </h1>
      {symptoms.length === 0 ? (
        <p className="text-gray-600 dark:text-gray-400">
          No behavioral changes recorded yet.
        </p>
      ) : (
        <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-4">
          {symptoms.map(([symptom, count]) => (
            <Card key={symptom}>
              <CardHeader>
                <CardTitle>{symptom}</CardTitle>
              </CardHeader>
              <CardContent>
                <p className="text-gray-700 dark:text-gray-300">
                  Reported {count} time{count > 1 ? 's' : ''}
                </p>
              </CardContent>
            </Card>
          ))}
        </div>
      )}
    </div>
  )
}
