'use client'

import { useState, useEffect } from 'react'

export interface SensorData {
  heartRate: number
  activityLevel: number
  temperature: number
  steps: number
  sleepHours: number
  caloriesBurned: number
  lastUpdated: Date
  batteryLevel: number
  signalStrength: number
}

export function useSensorData() {
  const [sensorData, setSensorData] = useState<SensorData>({
    heartRate: 85,
    activityLevel: 65,
    temperature: 101.5,
    steps: 2450,
    sleepHours: 8.5,
    caloriesBurned: 320,
    lastUpdated: new Date(),
    batteryLevel: 78,
    signalStrength: 92
  })

  const [isConnected, setIsConnected] = useState(true)

  useEffect(() => {
    const interval = setInterval(() => {
      try {
        setSensorData(prevData => ({
          heartRate: Math.max(60, Math.min(120, prevData.heartRate + (Math.random() - 0.5) * 10)),
          activityLevel: Math.max(0, Math.min(100, prevData.activityLevel + (Math.random() - 0.5) * 20)),
          temperature: Math.max(99, Math.min(103, prevData.temperature + (Math.random() - 0.5) * 0.5)),
          steps: prevData.steps + Math.floor(Math.random() * 10),
          sleepHours: Math.max(0, Math.min(12, prevData.sleepHours + (Math.random() - 0.5) * 0.1)),
          caloriesBurned: prevData.caloriesBurned + Math.floor(Math.random() * 5),
          lastUpdated: new Date(),
          batteryLevel: Math.max(0, Math.min(100, prevData.batteryLevel - 0.01)),
          signalStrength: Math.max(0, Math.min(100, prevData.signalStrength + (Math.random() - 0.5) * 5))
        }))

        // Simulate occasional connection issues
        setIsConnected(Math.random() > 0.05) // 95% uptime
      } catch (error) {
        console.error('Error updating sensor data:', error)
        setIsConnected(false)
      }
    }, 3000) // Update every 3 seconds

    return () => clearInterval(interval)
  }, [])

  return { sensorData, isConnected }
}
