'use client'

import { useState } from 'react'
import { usePetData } from '@/context/PetDataContext'
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card'
import { Button } from '@/components/ui/button'
import { toast } from 'sonner'

export default function OrderMedicine() {
  const { petData } = usePetData()
  const [orderingId, setOrderingId] = useState<string | null>(null)

  const activeMedications = petData.medications.filter(med => med.active)

  const handleOrder = (medId: string, medName: string) => {
    setOrderingId(medId)
    // Simulate order processing delay
    setTimeout(() => {
      setOrderingId(null)
      toast.success(`Order placed successfully for ${medName}`)
    }, 1500)
  }

  return (
    <div className="space-y-6">
      <h1 className="text-3xl font-bold text-gray-900 dark:text-white">
        Order Prescribed Medicine
      </h1>
      <p className="text-gray-600 dark:text-gray-400">
        Order medicines prescribed for your pet {petData.pet.name}.
      </p>

      {activeMedications.length === 0 ? (
        <p className="text-gray-500 dark:text-gray-400">
          No active medications to order.
        </p>
      ) : (
        <div className="space-y-4">
          {activeMedications.map((med) => (
            <Card key={med.id}>
              <CardHeader>
                <CardTitle>{med.name}</CardTitle>
              </CardHeader>
              <CardContent className="flex items-center justify-between">
                <div>
                  <p className="text-sm text-gray-700 dark:text-gray-300">
                    Dosage: {med.dosage}
                  </p>
                  <p className="text-sm text-gray-700 dark:text-gray-300">
                    Frequency: {med.frequency}
                  </p>
                </div>
                <Button
                  onClick={() => handleOrder(med.id, med.name)}
                  disabled={orderingId === med.id}
                >
                  {orderingId === med.id ? 'Ordering...' : 'Order'}
                </Button>
              </CardContent>
            </Card>
          ))}
        </div>
      )}
    </div>
  )
}
