'use client'

import { useState } from 'react'
import { usePetData } from '@/context/PetDataContext'
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/card'
import { Button } from '@/components/ui/button'
import { Input } from '@/components/ui/input'
import { Label } from '@/components/ui/label'
import { Badge } from '@/components/ui/badge'

export default function InsuranceDetails() {
  const { petData, dispatch } = usePetData()
  const [isEditing, setIsEditing] = useState(false)
  const [editedInsurance, setEditedInsurance] = useState(petData.insurance)

  const handleSave = () => {
    dispatch({
      type: 'UPDATE_INSURANCE',
      payload: editedInsurance
    })
    setIsEditing(false)
  }

  const handleCancel = () => {
    setEditedInsurance(petData.insurance)
    setIsEditing(false)
  }

  const isExpiringSoon = () => {
    const expirationDate = new Date(petData.insurance.expirationDate)
    const today = new Date()
    const daysUntilExpiry = Math.ceil((expirationDate.getTime() - today.getTime()) / (1000 * 60 * 60 * 24))
    return daysUntilExpiry <= 30 && daysUntilExpiry > 0
  }

  const isExpired = () => {
    const expirationDate = new Date(petData.insurance.expirationDate)
    const today = new Date()
    return expirationDate < today
  }

  const getStatusBadge = () => {
    if (isExpired()) {
      return <Badge variant="destructive">Expired</Badge>
    }
    if (isExpiringSoon()) {
      return <Badge className="bg-yellow-500 text-white">Expiring Soon</Badge>
    }
    return <Badge className="bg-green-500 text-white">Active</Badge>
  }

  const getDaysUntilExpiry = () => {
    const expirationDate = new Date(petData.insurance.expirationDate)
    const today = new Date()
    return Math.ceil((expirationDate.getTime() - today.getTime()) / (1000 * 60 * 60 * 24))
  }

  return (
    <div className="space-y-6">
      <div className="flex items-center justify-between">
        <div>
          <h1 className="text-3xl font-bold text-gray-900 dark:text-white">
            Insurance Details
          </h1>
          <p className="text-gray-600 dark:text-gray-400 mt-1">
            Manage {petData.pet.name}'s insurance policy information
          </p>
        </div>
        {!isEditing ? (
          <Button onClick={() => setIsEditing(true)}>
            Edit Insurance
          </Button>
        ) : (
          <div className="space-x-2">
            <Button variant="outline" onClick={handleCancel}>
              Cancel
            </Button>
            <Button onClick={handleSave}>
              Save Changes
            </Button>
          </div>
        )}
      </div>

      {/* Insurance Status Alert */}
      {(isExpired() || isExpiringSoon()) && (
        <Card className={`border-2 ${isExpired() ? 'border-red-200 dark:border-red-800' : 'border-yellow-200 dark:border-yellow-800'}`}>
          <CardHeader>
            <CardTitle className={`${isExpired() ? 'text-red-600 dark:text-red-400' : 'text-yellow-600 dark:text-yellow-400'}`}>
              {isExpired() ? 'Insurance Expired' : 'Insurance Expiring Soon'}
            </CardTitle>
            <CardDescription>
              {isExpired() 
                ? 'Your pet insurance policy has expired. Please renew immediately to maintain coverage.'
                : `Your pet insurance policy expires in ${getDaysUntilExpiry()} days. Please renew soon.`
              }
            </CardDescription>
          </CardHeader>
        </Card>
      )}

      {/* Main Insurance Card */}
      <Card>
        <CardHeader>
          <div className="flex items-center justify-between">
            <CardTitle className="text-xl">
              Insurance Policy Details
            </CardTitle>
            {getStatusBadge()}
          </div>
          <CardDescription>
            Current insurance coverage for {petData.pet.name}
          </CardDescription>
        </CardHeader>
        <CardContent>
          {!isEditing ? (
            <div className="grid md:grid-cols-2 gap-6">
              <div className="space-y-4">
                <div>
                  <Label className="text-sm font-medium text-gray-500 dark:text-gray-400">
                    Insurance Provider
                  </Label>
                  <p className="text-lg font-semibold text-gray-900 dark:text-white">
                    {petData.insurance.provider}
                  </p>
                </div>
                <div>
                  <Label className="text-sm font-medium text-gray-500 dark:text-gray-400">
                    Policy Number
                  </Label>
                  <p className="text-lg font-mono text-gray-900 dark:text-white">
                    {petData.insurance.policyNumber}
                  </p>
                </div>
                <div>
                  <Label className="text-sm font-medium text-gray-500 dark:text-gray-400">
                    Coverage Type
                  </Label>
                  <p className="text-lg text-gray-900 dark:text-white">
                    {petData.insurance.coverage}
                  </p>
                </div>
                <div>
                  <Label className="text-sm font-medium text-gray-500 dark:text-gray-400">
                    Contact Information
                  </Label>
                  <p className="text-lg text-gray-900 dark:text-white">
                    {petData.insurance.contactInfo}
                  </p>
                </div>
              </div>
              <div className="space-y-4">
                <div>
                  <Label className="text-sm font-medium text-gray-500 dark:text-gray-400">
                    Expiration Date
                  </Label>
                  <p className="text-lg text-gray-900 dark:text-white">
                    {new Date(petData.insurance.expirationDate).toLocaleDateString()}
                  </p>
                </div>
                <div>
                  <Label className="text-sm font-medium text-gray-500 dark:text-gray-400">
                    Annual Deductible
                  </Label>
                  <p className="text-lg text-gray-900 dark:text-white">
                    â¹{petData.insurance.deductible.toLocaleString('en-IN')}
                  </p>
                </div>
                <div>
                <Label className="text-sm font-medium text-gray-500 dark:text-gray-400">
                  Quarterly Premium
                </Label>
                  <p className="text-lg text-gray-900 dark:text-white">
                    â¹{petData.insurance.quarterlyPremium?.toLocaleString('en-IN') ?? 'N/A'}
                  </p>
                </div>
              <div>
                <Label className="text-sm font-medium text-gray-500 dark:text-gray-400">
                  Annual Cost
                </Label>
                <p className="text-lg font-semibold text-blue-600 dark:text-blue-400">
                  â¹{petData.insurance.quarterlyPremium ? (petData.insurance.quarterlyPremium * 4).toLocaleString('en-IN') : 'N/A'}
                </p>
              </div>
            </div>
          </div>
          ) : (
            <div className="grid md:grid-cols-2 gap-6">
              <div className="space-y-4">
                <div>
                  <Label htmlFor="provider">Insurance Provider</Label>
                  <Input
                    id="provider"
                    value={editedInsurance.provider}
                    onChange={(e) => setEditedInsurance({ ...editedInsurance, provider: e.target.value })}
                    placeholder="Insurance company name"
                  />
                </div>
                <div>
                  <Label htmlFor="policyNumber">Policy Number</Label>
                  <Input
                    id="policyNumber"
                    value={editedInsurance.policyNumber}
                    onChange={(e) => setEditedInsurance({ ...editedInsurance, policyNumber: e.target.value })}
                    placeholder="Policy number"
                  />
                </div>
                <div>
                  <Label htmlFor="coverage">Coverage Type</Label>
                  <Input
                    id="coverage"
                    value={editedInsurance.coverage}
                    onChange={(e) => setEditedInsurance({ ...editedInsurance, coverage: e.target.value })}
                    placeholder="e.g., Comprehensive, Basic"
                  />
                </div>
                <div>
                  <Label htmlFor="contactInfo">Contact Information</Label>
                  <Input
                    id="contactInfo"
                    value={editedInsurance.contactInfo}
                    onChange={(e) => setEditedInsurance({ ...editedInsurance, contactInfo: e.target.value })}
                    placeholder="Phone number or website"
                  />
                </div>
              </div>
              <div className="space-y-4">
                <div>
                  <Label htmlFor="expirationDate">Expiration Date</Label>
                  <Input
                    id="expirationDate"
                    type="date"
                    value={editedInsurance.expirationDate}
                    onChange={(e) => setEditedInsurance({ ...editedInsurance, expirationDate: e.target.value })}
                  />
                </div>
                <div>
                  <Label htmlFor="deductible">Annual Deductible (â¹)</Label>
                  <Input
                    id="deductible"
                    type="number"
                    value={editedInsurance.deductible}
                    onChange={(e) => setEditedInsurance({ ...editedInsurance, deductible: parseFloat(e.target.value) || 0 })}
                    placeholder="Annual deductible amount"
                  />
                </div>
                <div>
                  <Label htmlFor="quarterlyPremium">Quarterly Premium (â¹)</Label>
                  <Input
                    id="quarterlyPremium"
                    type="number"
                    value={editedInsurance.quarterlyPremium}
                    onChange={(e) => setEditedInsurance({ ...editedInsurance, quarterlyPremium: parseFloat(e.target.value) || 0 })}
                    placeholder="Quarterly premium amount"
                  />
                </div>
                <div>
                  <Label className="text-sm font-medium text-gray-500 dark:text-gray-400">
                    Estimated Annual Cost
                  </Label>
                  <p className="text-lg font-semibold text-blue-600 dark:text-blue-400">
                    â¹{(editedInsurance.quarterlyPremium * 4).toLocaleString()}
                  </p>
                </div>
              </div>
            </div>
          )}
        </CardContent>
      </Card>

      {/* Insurance Summary */}
      <Card>
        <CardHeader>
          <CardTitle>Insurance Summary</CardTitle>
          <CardDescription>Key insurance metrics and information</CardDescription>
        </CardHeader>
        <CardContent>
          <div className="grid grid-cols-2 md:grid-cols-4 gap-4">
          <div className="text-center p-4 bg-blue-50 dark:bg-blue-900/20 rounded-lg">
            <div className="text-2xl font-bold text-blue-600 dark:text-blue-400">
              â¹{petData.insurance.quarterlyPremium?.toLocaleString('en-IN') ?? 'N/A'}
            </div>
            <div className="text-sm text-gray-600 dark:text-gray-400">
              Quarterly Premium
            </div>
          </div>
          <div className="text-center p-4 bg-green-50 dark:bg-green-900/20 rounded-lg">
            <div className="text-2xl font-bold text-green-600 dark:text-green-400">
              â¹{petData.insurance.deductible.toLocaleString('en-IN')}
            </div>
            <div className="text-sm text-gray-600 dark:text-gray-400">
              Annual Deductible
            </div>
          </div>
          <div className="text-center p-4 bg-purple-50 dark:bg-purple-900/20 rounded-lg">
            <div className="text-2xl font-bold text-purple-600 dark:text-purple-400">
              â¹{petData.insurance.quarterlyPremium ? (petData.insurance.quarterlyPremium * 4).toLocaleString('en-IN') : 'N/A'}
            </div>
            <div className="text-sm text-gray-600 dark:text-gray-400">
              Annual Cost
            </div>
          </div>
            <div className="text-center p-4 bg-orange-50 dark:bg-orange-900/20 rounded-lg">
              <div className="text-2xl font-bold text-orange-600 dark:text-orange-400">
                {Math.abs(getDaysUntilExpiry())}
              </div>
              <div className="text-sm text-gray-600 dark:text-gray-400">
                {getDaysUntilExpiry() > 0 ? 'Days Until Expiry' : 'Days Expired'}
              </div>
            </div>
          </div>
        </CardContent>
      </Card>

      {/* Quick Actions */}
      <Card>
        <CardHeader>
          <CardTitle>Quick Actions</CardTitle>
          <CardDescription>Common insurance-related tasks</CardDescription>
        </CardHeader>
        <CardContent>
          <div className="grid md:grid-cols-2 gap-4">
            <div className="p-4 border border-gray-200 dark:border-gray-700 rounded-lg">
              <h3 className="font-semibold text-gray-900 dark:text-white mb-2">
                File a Claim
              </h3>
              <p className="text-sm text-gray-600 dark:text-gray-400 mb-3">
                Submit a new insurance claim for veterinary expenses.
              </p>
              <Button variant="outline" className="w-full">
                Start Claim Process
              </Button>
            </div>
            <div className="p-4 border border-gray-200 dark:border-gray-700 rounded-lg">
              <h3 className="font-semibold text-gray-900 dark:text-white mb-2">
                Contact Insurance
              </h3>
              <p className="text-sm text-gray-600 dark:text-gray-400 mb-3">
                Get in touch with your insurance provider for questions or support.
              </p>
              <Button variant="outline" className="w-full">
                Contact {petData.insurance.provider}
              </Button>
            </div>
          </div>
        </CardContent>
      </Card>
    </div>
  )
}
