'use client'

import { useState } from 'react'
import Header from '@/components/Header'
import Sidebar from '@/components/Sidebar'
import PetDashboard from '@/components/PetDashboard'
import HealthRecords from '@/components/HealthRecords'
import ActivitiesLog from '@/components/ActivitiesLog'
import Medications from '@/components/Medications'
import OrderMedicine from '@/components/OrderMedicine'
import BehaviorChangesDashboard from '@/components/BehaviorChangesDashboard'
import Vaccinations from '@/components/Vaccinations'
import InsuranceDetails from '@/components/InsuranceDetails'
import PetProfile from '@/components/PetProfile'
import CustomerService from '@/components/CustomerService'

type ActiveSection = 'dashboard' | 'health' | 'activities' | 'medications' | 'orderMedicine' | 'behaviorChanges' | 'vaccinations' | 'insurance' | 'profile' | 'support'

export default function DashboardPage() {
  const [activeSection, setActiveSection] = useState<ActiveSection>('dashboard')

  const renderActiveSection = () => {
    switch (activeSection) {
      case 'dashboard':
        return <PetDashboard />
      case 'health':
        return <HealthRecords />
      case 'activities':
        return <ActivitiesLog />
      case 'medications':
        return <Medications />
      case 'orderMedicine':
        return <OrderMedicine />
      case 'behaviorChanges':
        return <BehaviorChangesDashboard />
      case 'vaccinations':
        return <Vaccinations />
      case 'insurance':
        return <InsuranceDetails />
      case 'profile':
        return <PetProfile />
      case 'support':
        return <CustomerService />
      default:
        return <PetDashboard />
    }
  }

  return (
    <div className="min-h-screen bg-gray-50 dark:bg-gray-900">
      <Header />
      <div className="flex">
        <Sidebar activeSection={activeSection} onSectionChange={setActiveSection} />
        <main className="flex-1 p-6">
          {renderActiveSection()}
        </main>
      </div>
    </div>
  )
}
